/*登录页 用户名框自动获取焦点 按enter实现登录效果*/
$(function(){
    $("#username").focus();
    $(document).keyup(function(event){
        if(event.keyCode==13){
            $("#login_btn").click();
        }
    });
});
/*注册页*/
$(function(){
	//聚焦失焦input
	$('#i1').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("支持字母，数字的组合,3-10个字符");
		}
	})
	$('#i2').focus(function(){
		if($(this).val().length==0){
		    $(this).parent().next("div").text("使用数字，3-6个字符");
		}
	})
	$('#i3').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("请再次输入密码");
		}
    })
    $('#i4').focus(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("验证完后，你可以使用该手机登陆和找回密码");
		}
	})
	$('#i5').focus(function(){	
		if($(this).val().length==0){
			$(this).parent().next().next("div").text("看不清？点击图片更换验证码");
		}
	})
	//input各种判断
	//用户名：
	$('#i1').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val().length>=3&& !isNaN($(this).val())){
			$(this).parent().next("div").text("用户名不能为纯数字");
			$(this).parent().next("div").css("color",'red');
		}else{
            $(this).parent().next("div").text("");
        }
	})
	//密码
	$('#i2').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val().length<3 && $(this).val().length>6){
			$(this).parent().next("div").text("长度只能在3-6个字符之间");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
//	确认密码
	$('#i3').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if($(this).val()!=$('#i2').val()){
			$(this).parent().next("div").text("两次密码不匹配");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
//	手机号
	$('#i4').blur(function(){
        var preg=/^1[3-8]\d{9}$/;
        var p=$(this).val();
		if($(this).val().length==0){
			$(this).parent().next("div").text("");
			$(this).parent().next("div").css("color",'#000');
		}else if(!preg.test(p)){
			$(this).parent().next("div").text("手机号格式不正确");
			$(this).parent().next("div").css("color",'red');
		}else{
			$(this).parent().next("div").text("");
		}		
	})
// 	验证码
//	 验证码刷新
	function code(){
		var str="qwertyuiopasdfghjklzxcvbnm1234567890QWERTYUIOPLKJHGFDSAZXCVBNM";
		var str1=0;
		for(var i=0; i<4;i++){
			str1+=str.charAt(Math.floor(Math.random()*62)) 
		}
		str1=str1.substring(1)
		$("#code").text(str1);
	}
	code();
	$("#code").click(code);	
//	验证码验证
	$('#i5').blur(function(){
		if($(this).val().length==0){
			$(this).parent().next().next("div").text("");
			$(this).parent().next().next("div").css("color",'#000');
		}else if($(this).val().toUpperCase()!=$("#code").text().toUpperCase()){
			$(this).parent().next().next("div").text("验证码不正确");
			$(this).parent().next().next("div").css("color",'red');
		}else{
			$(this).parent().next().next("div").text("");
		}		
	})
})
