<template>
    <div id="container">
        <img class="heng" src="img/login/login_bg.gif" alt="哼">
        <div id="output">
		    <div class="containerT">
                <h1>用户登录</h1>
                <form class="form" id="f1">
                    <input type="text" placeholder="用户名" id="username" v-model="uname" @keyup="getInputval">
                    <input type="password" placeholder="密码" id="password" v-model="upwd" @keyup="getInputval">
                    <button type="button" id="login_btn" :disabled="isLogin" @click="btn">登录</button>&nbsp;
                    <router-link to="/reg" id="reg_btn">注册</router-link>
                </form>
		    </div>
	    </div>
    </div>
</template>
<script>
import vue from 'vue'
export default {
    data(){
        return {
            uname:"",
            upwd:"",
            isLogin:true,
        }
    },
    methods:{
        //登录按钮是否禁用
        getInputval(){
            if(this.uname.trim().length>1 && this.upwd.trim().length>1){
                //取消禁用
                this.isLogin=false;
            }else{
                this.isLogin=true;
            }
        },
        btn(){
            var uname=this.uname;
            var upwd=this.upwd;
            //判断用户名格式是否正确
            var preg=/^[0-9A-Za-z]{3,10}$/;
            if(!preg.test(uname)){
                alert("用户名格式不正确");
                return;
            }
            //判断密码格式是否正确
            var wreg=/^\d{3,6}$/;
            if(!wreg.test(upwd)){
                alert("密码格式不正确");
                return;
            }
            //向服务器发送ajax请求
            var url="users?username="+uname+"&password="+upwd;
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                    console.log(res.data);
                    alert(res.data.msg);
                    this.$router.push('/index');
                }else if(res.data.code==-1){
                    alert(res.data.msg);
                    this.uname="";
                    this.upwd="";
                }
            })
        }
    }
}
</script>
<style scoped>
    *{
        margin: 0;
        padding: 0;
        font-size: 18px;
        font-family:microsoft yahei;
    }
    body{
        width: 100%;
        height: 1217px;
        background: #fff;
    }
    .heng{
        width:200px;
        height: 250px;
        position: fixed;
        top:5%;
        margin-left: -100px;
    }
    #container{
        width:100%;
        height:100%;
        position:fixed;
        top:0;left:0;
        z-index:999;
        background:url("/img/login/login_bj.jpg") no-repeat;
        background-size: 100% 100%;
    }
    #output{
        width:100%;
        height:100%
    }
    .containerT{
        width:400px;
        height:300px;
        text-align:center;
        position:absolute;
        top:50%;left:50%;
        margin:-150px 0 0 -200px;
        border-radius:3px;
        border: 1px solid #fff;
        padding-top: 10px;
    }
    .containerT h1{
        font-size:20px;
        font-family:microsoft yahei,Arial,sans-serif;
        transition-duration:1s;
        transition-timing-function:ease-in-put;
        font-weight:500;
        position: relative;
        top:30px;
    }
    form{
        padding:20px 0;
        position:relative;
        z-index:2
    }
    form input{
        outline:0;
        border:1px solid rgba(255,255,255,.4);
        background-color:rgba(255,255,255,.2);
        width:200px;
        border-radius:3px;
        padding:8px 15px;
        margin:0 auto 10px;
        display:block;
        text-align:center;
        font-size:15px;
        color:#fff;
        transition-duration:.25s;
        font-weight:300;
        position: relative;
        top:40px
    }
    form input:hover{
        background-color:rgba(255,255,255,.4)
    }
    form input:focus{
        background-color:#fff;
        width:230px;
        color:#333
    }
    form button{
        margin-top: 20px;
        position:relative;
        top:40px;
    }
    form #login_btn{
        outline:0;
        background-color:#fff;
        border:0;
        padding:10px 15px;
        color:#333;
        border-radius:3px;
        width:100px;
        cursor:pointer;
        font-family:microsoft yahei,Arial,sans-serif;
        font-size:16px;
        font-weight:700;
    }
    form #reg_btn{
        display: inline-block;
        outline:0;
        background-color:#fff;
        border:0;
        padding:10px 15px;
        color:#333;
        border-radius:3px;
        width:100px;
        cursor:pointer;
        font-family:microsoft yahei,Arial,sans-serif;
        font-size:16px;
        font-weight:700;
        position: relative;
        top:40px;
    }
    form button:hover,a:hover{
        background-color:#ccc !important;
        text-decoration: none;
    }
</style>