<template>
    <div class="reg_container">
        <my-header></my-header>
        <header>
            <img src="img/index/logo.png" alt="飞车logo">
        </header>
        <form action="">
            <div class="register-box">
                <label for="username" class="username_label">用 户 名
                    <input id="i1" maxlength="20" type="text" placeholder="您的用户名和登录名" v-model="uname">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">设 置 密 码
                    <input id="i2" maxlength="20" type="password" placeholder="您的登录密码" v-model="password">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">确 认 密 码
                    <input id="i3" maxlength="20" type="password" placeholder="请再次输入密码">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_babel pr-2">中 国 0086
                    <input id="i4" class="phone" maxlength="20" type="text" placeholder="建议使用常用手机" v-model="tel">
                </label>
                <div class="tips"></div>
            </div>
            <div class="register-box">
                <label for="username" class="other_label">验 证 码
                    <input id="i5" maxlength="20" type="text" placeholder="请输入验证码">
                </label>
                <span id="code"></span>
                <div class="tips"></div>
            </div>
            <div class="arguement">
                <input type="checkbox" id="xieyi">阅读并同意
                    <a href="javascript:;">《飞车用户注册协议》</a>
                    <a href="javascript:;">《隐私政策》</a>
                <div class="tips"></div>
            </div>
            <div class="submit_btn">
                <button type="submit" id="submit_btn" @click="submit_btn">立 即 注 册</button>
            </div>
        </form>
        <my-footer></my-footer>
    </div>
</template>
<script>
import vue from 'vue'
import myHeader from '@/components/header.vue'
import myFooter from '@/components/footer.vue'
export default {
    data(){
        return {
            uname:"",
            password:"",
            tel:"",
        }
    },
    methods:{
        submit_btn(e){
            var uname=this.uname;
            var password=this.password;
            var tel=this.tel;
            //判断用户名格式是否正确
            var preg=/^[0-9A-Za-z]{3,10}$/;
            if(!preg.test(uname)){
                alert("用户名格式不正确");
                return;
            }
            //判断密码格式是否正确
            var wreg=/^\d{3,6}$/;
            if(!wreg.test(password)){
                alert("密码格式不正确");
                return;
            }
            //判断手机格式是否正确
            var treg=/^1[3-8]\d{9}$/;
            if(!treg.test(tel)){
                alert("手机号格式不正确");
                return;
            }
            //判断协议是否选中
            if(!$("#xieyi")[0].checked){						
                $("#xieyi").next().next().next(".tips").text("请勾选协议");
                $("#xieyi").next().next().next(".tips").css("color",'red');
                e.preventDefault();
                return;
            }
            //发送ajax请求
            var url="reg?username="+uname+"&password="+password+"&tel="+tel;
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                    alert(res.data.msg);
                    this.$router.push("/index");
                }
            })
        }
    },
    components:{
        myFooter,
        myHeader,
    }
}
</script>
<style scoped>
    /*重置默认的外边距和内边距*/
*{
    margin:0;
    padding:0;
}
/*常用标签的样式重置*/
a{
    text-decoration: none;
}
input{
    outline: none;
    border:none;
}
/*设定html和body的宽度为100%*/
html,body{
    width:100%; 
    height: 100%;
    font: 14px/150% tahoma,arial,Microsoft YaHei,Hiragino Sans GB,sans-serif;
    color: #666;
}
.reg_container{
    background: url("/img/reg/reg_bj.jpg") no-repeat;
    background-size: 100% 100%;
}
/*header*/
header>img{
    position: absolute;
    left: 20%;
    top:8%;
}
/*注册信息*/
/*总体布局*/
form{
    position: relative;
    left: 70%;
	width: 400px;
    padding-top: 100px;
    padding-bottom: 50px;
}
.register-box{
	border: 1px solid #ddd;
    margin-bottom: 32px;
}
.register-box label{
	height: 52px;
    line-height: 52px;
    display: inline-block;
}
.register-box input{
	width: 190px;
    height: 19px;
    padding-bottom: 11px;
    padding-left: 20px;
    padding-top: 16px;
    font-size: 14px;
    color: #666;
    background: transparent;
    font-family: "Microsoft YaHei","Hiragino Sans GB";
}
.register-box .tips{
	position: absolute;
    margin-top: 5px;
    font-size: 14px;
    color: #000;
}
/*用户名*/
.register-box .username_label{
	word-spacing: 10px;
}
/*密码，确认密码，*/
.register-box .other_label{
	word-spacing: 2px;
}
/*手机号*/
.register-box span{
    display: inline-block;
    width: 77px;
    height: 52px;
    border-right: 1px solid #ddd;
}
.register-box .phone{
	padding-left: 28px;
}
/*验证码*/
#code{
	display: inline-block;
    width: 100px;
    height: 46px;
    vertical-align: middle;
    cursor: pointer;
    font-size: 25px;
    color: #000;
    text-align: center;
    line-height: 46px;
}
/*协议*/
.arguement{
	margin-bottom: 58px;
}
.arguement input{
	vertical-align: middle;
}
.arguement a{
	color: #38f;
    text-decoration: none;
    font-size: 12px;
}
/*提交按钮*/
.submit_btn button{
	width: 100%;
    height: 54px;
    color: #fff;
    background: #e22;
    cursor: pointer;
    border: 0;
    font-size: 16px;
    font-family: "Microsoft YaHei","Hiragino Sans GB";
}
</style>