        },
        lclick(lis){
            this.lis=lis;
        },
        liclick(lies){
            this.lies=lies;
        }
    },
    created(){
        //设置定时器 切换index值 更改显示内容
        setInterval(()=>{
            if(this.index<4){
                this.index++;
            }else{
                this.index=1;
            }
        },3000)
    },
    destroyed(){
        //每次离开当前界面时,清除定时器
        clearInterval(this.setTimer);
    },
    mounted(){
        new Swiper('.newtrophy-swiper-car',{
            loop:true,
            autoplay:true,
            observer:true,
            navigation: { 
                nextEl: '.swiper-button-next1', 
                prevEl: '.swiper-button-prev1',
