            loop:true,
            observer:true,
            navigation: { 
                nextEl: '.swiper-button-next3', 
                prevEl: '.swiper-button-prev3',
            },
        })
    },
    /*将header.vue和footer.vue设置为当前组件的子组件*/
    components:{
        myFooter,
        myHeader,
    }
}
</script>
