<template>
    <div class="app_footer">
        <p v-for="(item,i) of p0" :key="i">
            <a href="javascript:;">{{item.title}}</a>&nbsp;|&nbsp;
        </p>
        <p><a href="javascript:;">{{p1.title}}</a></p><br>
        <p>{{p2.title}}</p><br>
        <p>{{p3.title}}</p>
    </div>
</template>
<script>
export default {
    data(){
        return {
            p0:[],
            p1:{},
            p2:{},
            p3:{},
        }
    },
    created(){
        this.axios.get("footer").then(res=>{
            this.p0=res.data.slice(0,8);
            this.p1=res.data[8];
            this.p2=res.data[9];
            this.p3=res.data[10];
        })
    }
}
</script>
<style scoped>
    .app_footer{
        width: 100%;
        height: 160px;
        background: #000;
        padding-top: 50px;
    }
    .app_footer>p,a{
        display:inline-block;
        color: #fff;
        font-size: 14px;
        margin: 0;
    }
</style>